@media (max-width: 1024px) {
  .skills {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    .wrap {
      width: 100%;
      min-height: auto;
  
  
      .wrap_skills {
        // padding: 20px;
        width: 100%;
        margin: auto;
        // background-color: rgb(162, 182, 89);
        border-radius: 15px;
  
        .title_skills {
          color: #000000;
          font-size: 40px;
          padding: 20px;
          text-align: center;
          font-weight: bold;
          margin-bottom: 80px;
  
          .title_skills_small {
            margin: auto;
            width: 400px;
            color: #000000;
            font-size: 20px;
            // padding: 20px;
            text-align: center;
            font-weight: normal;
  
          }
        }
  
  
        .ability {
         
  
          .image_wrap_skills {
            max-height: 100%;
            padding-bottom: 20px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
  
            .tools_cover_skills_blue {
              background-color: rgb(191, 226, 255);
              width: 28%;
              padding: 20px;
              border-radius: 15px;
              height: 200px;
  
