                width: 70px;
                height: 100px;
                margin: auto;
  
                .image_skills {
                  width: 70px;
  
                }
              }
  
              .wrap_ww_skills {
                .write_skills {
                  font-weight: bold;
                  font-size: 20px;
                  color: rgb(0, 0, 0);
                  text-align: center;
                }
  
                .write_skills2 {
                  text-align: center;
                  font-size: 20px;
                  color: rgb(0, 0, 0);
                }
              }
  
              // &:hover {
              //   background-color: rgb(191, 194, 202);
              //   color: rgb(255, 255, 255);
              //   filter: brightness(100%);
              // }
  
            }
  
            .tools_cover_skills_pink {
  
             
              .image_cover_skills2 {
               
                .image_skills2 {
                  width: 70px;
  
                }
              }
  
              .wrap_ww_skills2 {
                .write_skills2 {
                  font-weight: bold;
                  font-size: 20px;
                  color: rgb(0, 0, 0);
                  text-align: center;
                }
  
                .write_skills22 {
                  font-size: 20px;
                  color: rgb(0, 0, 0);
                  text-align: center;
                }
              }
  
  
