.education {
  //background-color:#EE696B #523A78;
  // background-image: linear-gradient(to right, #faa287 ,#709e34, rgb(102, 102, 37));
  width: 100%;
  min-height: auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-bottom: 50px;

  .edu_wrap {
    .edu_title {
      color: #000000;
      font-size: 40px;
      padding: 50px;
      // text-align: center;
      font-weight: bold;
    }

    .cover_img_edu {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .cover_edu_img_hs {
       
        border-radius: 15px;
        align-items: center;
        width: 400px;
        height: 400px;
        background-color: rgb(255, 255, 255);
        margin: 20px;
        
        .img_hs {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          align-items: center;
          margin: auto;
          border-radius: 50%;
          width: 200px;
          height: 200px;
          background-color: rgb(253, 137, 156);

        }

        .hs {
          font-weight: bold;
          text-align: center;
          font-size: 22px;
          color: rgb(16, 87, 16);
          padding: 4px;

          .detail_hs {
            font-weight: normal;
            text-align: center;
            font-size: 17px;
            color: rgb(16, 87, 16);
            padding: 4px;
          }
        }




      }

      .cover_edu_img_uni {
        border-radius: 15px;
        width: 400px;
        height: 400px;
        background-color: rgb(255, 255, 255);
        margin: 20px;
       
        .img_uni {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          margin: auto;
          border-radius: 50%;
          width: 200px;
          height: 200px;

        }

        .university {
          font-weight: bold;
          text-align: center;
          font-size: 22px;
          color: rgb(16, 87, 16);
          padding: 4px;

          .detail_uni {
            font-weight: normal;
            text-align: center;
            font-size: 17px;
            color: rgb(16, 87, 16);
            padding: 4px;
          }
        }


      }

    }

  }
}

@import'./responsive/educationRes.scss';